:root {
  --white: hsl(0, 0%, 100%);
  --darkWhite: hsl(0, 0%, 95%);
  --gray: hsl(231, 8%, 61%);
  --lightGray: hsl(223, 18%, 96%);
  --blue: hsl(231, 26%, 24%);
  --lightBlue: hsl(192, 69%, 51%);
  --green: hsl(136, 64%, 51%);
  --lightGreen: hsl(158, 61%, 49%);
}

:root {
  --font-public: "Public Sans", sans-serif;
}

html {
  font-size: 62.5%;
  background-color: var(--darkWhite);
}

.container {
  max-width: 32.7rem;
  width: 100%;
  height: 100%;
  margin: 0 auto;
}

.sec-title {
  text-align: center;
  display: flex;
  flex-direction: column;
  row-gap: 1.4rem;
}

/* Box sizing rules */
*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Remove default margin */
body,
h1,
h2,
h3,
h4,
p,
figure,
blockquote,
dl,
dd,
ul {
  margin: 0;
  padding: 0;
}

/* Remove list styles on ul, ol elements with a list role, which suggests default styling will be removed */
ul[role=list],
ol[role=list] {
  list-style: none;
}

li {
  list-style: none;
}

/* Set core root defaults */
html:focus-within {
  scroll-behavior: smooth;
}

/* Set core body defaults */
body {
  min-height: 100vh;
  text-rendering: optimizeSpeed;
  line-height: 1.5;
}

/* A elements that don't have a class get default styles */
a:not([class]) {
  text-decoration-skip-ink: auto;
}

/* Make images easier to work with */
img,
picture {
  max-width: 100%;
  display: block;
}

/* Inherit fonts for inputs and buttons */
input,
button,
textarea,
select {
  font: inherit;
}

/* Remove all animations, transitions and smooth scroll for people that prefer not to see them */
@media (prefers-reduced-motion: reduce) {
  html:focus-within {
    scroll-behavior: auto;
  }

  *,
*::before,
*::after {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }
}
body {
  font-family: var(--font-public);
}

.heading-primary {
  font-size: 4rem;
  font-weight: 300;
  color: var(--blue);
  line-height: 1.3;
}

.description {
  font-size: 1.5rem;
  color: var(--gray);
  line-height: 1.6;
}

.heading-secondary {
  font-size: 3.2rem;
  color: var(--blue);
  font-weight: 300;
  line-height: 1.3;
  text-align: center;
}

.heading-tertiary {
  font-size: 2rem;
  font-weight: 300;
}

.heading-fourth {
  font-size: 1.6rem;
  color: var(--blue);
  font-weight: 300;
}

.button {
  color: var(--white);
  border: none;
  padding: 0.8rem 3rem;
  border-radius: 2.2rem;
  font-size: 1.4rem;
  font-weight: 700;
  background-image: linear-gradient(to right bottom, var(--green), var(--lightBlue));
}

.header {
  position: fixed;
  top: 0;
  z-index: 1000;
  width: 100%;
  height: 6.4rem;
  background-color: var(--white);
}
.header__nav {
  height: 100%;
}
.header__nav-menu {
  position: absolute;
  top: -500%;
  width: 100%;
  background-color: var(--white);
  padding: 3.2rem 0;
  border-radius: 0.4rem;
}
.header__nav-list {
  row-gap: 2.4rem;
  font-size: 1.8rem;
}
.header__nav-link {
  color: var(--blue);
}

.nav {
  display: flex;
  align-items: center;
  position: relative;
  justify-content: space-between;
}
.nav__list {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.nav__link {
  text-decoration: none;
}
.nav__button {
  display: none;
}

.footer {
  padding: 4rem 0;
  background-color: var(--blue);
}
.footer__container {
  display: grid;
  row-gap: 3.2rem;
  justify-items: center;
}
.footer__share {
  display: flex;
  column-gap: 1.6rem;
}
.footer__nav {
  font-size: 1.5rem;
}
.footer__nav-menu {
  display: grid;
  justify-items: center;
  row-gap: 0.8rem;
}
.footer__nav-link {
  color: var(--white);
}

.copyright {
  display: block;
  margin-top: 2.4rem;
  color: var(--white);
}

.home {
  position: relative;
  padding-bottom: 3.6rem;
}
.home__container {
  display: flex;
  flex-direction: column-reverse;
  transform: translateY(-5.2rem);
  row-gap: 2rem;
}
.home__text-content {
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  row-gap: 1.6rem;
}
.home__description {
  margin-bottom: 1.6rem;
}
.home__img-content {
  background-image: url("../images/bg-intro-mobile.svg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 0 3.8rem;
  padding: 0 2.4rem;
}

.feature {
  padding: 6.4rem 0;
  background-color: var(--lightGray);
}
.feature__container {
  display: grid;
  row-gap: 3.2rem;
  padding-top: 5.6rem;
}
.feature__content {
  display: grid;
  justify-items: center;
  text-align: center;
  row-gap: 1.6rem;
}
.feature__title {
  margin-top: 0.8rem;
}

.article {
  padding: 8.8rem 0;
}
.article__container {
  display: grid;
  row-gap: 2.4rem;
}
.article__content {
  background-color: var(--white);
  border-radius: 5px;
}
.article__heading {
  margin-bottom: 3rem;
}
.article__img {
  border-top-left-radius: 5px;
  border-top-right-radius: 5px;
}
.article__text {
  padding: 2.4rem 3rem;
  display: flex;
  flex-direction: column;
  row-gap: 0.8rem;
}
.article__author {
  font-size: 1rem;
  font-weight: 400;
  color: var(--gray);
}
.article__description {
  font-weight: 400;
}